@import url('https://fonts.googleapis.com/css2?family=Poppins:ital,wght@0,400;0,600;0,700;1,200&display=swap');

body {
    font-family: "Poppins", Arial, Helvetica, sans-serif;
    margin: 0;
    font-size: 18px;
    font-weight: 400;
    line-height: 27px;
    background-image: url("./background.png");
    background-repeat: no-repeat;
    background-size: cover;
    align-items: center;
}

.content {
    display: flex;
    align-items: center;
    justify-content: center;
    height: 100vh;
}

main {
    transform: scale(0.8);
}

h1,
h2, 
h3 {
    font-weight: 700;
}

.question {
    font-weight: 700;
    font-size: 24px;
    color: rgb(3, 3, 149);
    padding-bottom: 8px;
}

.container {
    padding: 12px;
    max-width: 464px;
}

p {
    margin: 0;
    padding: 0;
}

.article {
    padding-top: 68px;
    padding-bottom: 68px;
    padding-left: 32px;
    padding-right: 32px;
    border-radius: 24px;
    background-color: #fff;
    max-width: 464px;
}

.wrapper {
    display: flex;
    flex-direction: column;
    gap: 24px;
}

.adventureImg {
    align-self: flex-end;
    margin-right: -32px;
    width: 162px;
    height: 116px;
    margin-top: -140px;
}

img {
    max-width: 100%;
}

button {
    font-size: 18px;
    line-height: 27px;
    border-radius: 12px;
    background-color: white;
    line-height: 1;
    cursor: pointer;
    font-family: "Poppins", Arial, Helvetica, sans-serif;
}

button:hover {
    background-color: #F9A826;
    outline: none;
    border: none;
    color: white;
}

button:focus {
    outline: none;
}


.winning-image {
    width: 238px;
    max-height: 129px;
}

.next-button-container {
    text-align: right;
}

.random-btn {
    width: 116px;
    height: 56px;
    border: none;
    background: #f9a826;
    box-shadow: 0 2px 4px rgba(252,168,47,.4);
    border-radius: 12px;
    font-weight: 700;
    font-size: 18px;
    color: #fff;
    line-height: 27px;
    padding-right: 36px;
    padding-left: 37px;
}

.recommendation-btn {
    padding: 18px 61px;
    color: #1D355D;
    padding-left: 59px;
    padding-right: 58px;
    padding-top: 18px;
    padding-bottom: 17px;
    max-width: 209px;
}

.options {
    display: grid;
    gap: 31px;
}


.option-btn {
    width: 100%;
    height: 56px;
    font-weight: 500;
    padding-left: 19px;
    padding-right: 0;
    text-align: left;
    border: 2px solid rgba(96, 102, 208, 0.7);
    color: rgba(96, 102, 208, 0.8);
    display: grid;
    grid-template-columns: .3fr 3fr;
    gap: 23px;
    align-items: center;
    
    &-content {
        padding-right: 17px;
    }
}

.alphabet {
    max-width: 19px;
}

.header {
    padding-bottom: 10px;
    padding-top: 20px;

    h1 {
        color: white;
        font-size: 36px;
        line-height: 52px;
        text-transform: uppercase;
        margin: 0;
        padding-bottom: 4rem;
        
        @media (min-width: 650px) {
            padding: 0;
        }
    }
}

.final-result {
    text-align: center;
    color: #1D355D;
    padding-bottom: 33px;
    
    button {
        color: #1D355D;
        border: 2px solid #1D355D;
        font-weight: 600;
        font-size: 18px;
    }
    h2 {
        margin: 0;
        padding-top: 72px;
        font-size: 48px;
        line-height: 72px;
    }
}

.correctBtn {
    background-color: #60BF88;
    color: #fff;
    background-image: url("./assets/done-24px.svg");
    background-repeat: no-repeat;
    background-position: 95%;
    border: none;
    padding-right: 30px;
}

.correctBtn:hover {
    background-color: #60BF88;
}

.wrongBtn {
    background-color: #EA8282;
    color: #fff;
    background-image: url("./assets/close-24px.svg");
    background-repeat: no-repeat;
    background-position: 95%;
    border: none;
    padding-right: 30px;

}

.wrongBtn:hover {
    background-color: #EA8282;
}

.scoreGuess {
    font-weight: 400;
    font-size: 18px;
    padding-bottom: 71px;
}

.score {
    font-size: 2rem;
    font-weight: 700;
    color: rgb(3, 202, 103);
}

button:disabled {
    cursor: not-allowed;
}

.flag-img {
    width: 84px;
    height: 54px;
    -webkit-filter: drop-shadow(0 4px 24px rgba(0,0,0,.1));
    filter: drop-shadow(0 4px 24px rgba(0,0,0,.1));
}

a {
    color: currentColor;
    text-decoration: none;

    &:hover {
        text-decoration: underline;
    }
}